import * as React from "react"
import AboutSnippet from "../components/about_snippet"
import SectionHeader from "../components/section_header"
import ProjectSection from "../components/project_section"
import ImageSection from "../components/image_section"
import Header from "../components/header"
import Footer from "../components/footer"
import style from "../styles.css"
import { useState } from 'react'
import * as Constants from "../constants"
import isNarrowWindow from "../helpers/is_narrow_window"

// markup
export default function IndexPage() {
  const [hoverTitle, setHoverTitle] = useState("")
  const [narrowWindow, setNarrowWindow] = useState(isNarrowWindow)

  setNarrowWindow(isNarrowWindow())
  if (typeof window !== `undefined`){
    window.addEventListener('resize', () => setNarrowWindow(isNarrowWindow()))
  }
  return (
    <main style={style}>
      <title>Ashley Wong</title>
      <Header narrowWindow={narrowWindow}/>
      <AboutSnippet narrowWindow={narrowWindow}/>
      <SectionHeader 
        title={Constants.UI_DESIGN}
        currentTitle={hoverTitle}
        supportedTitles={[Constants.CP_COMPANY, Constants.SWISS_FILMS]}/>
      <ImageSection
        narrowWindow={narrowWindow}
        currentTitle={hoverTitle}
        supportedTitles={[Constants.CP_COMPANY, Constants.SWISS_FILMS]}>
        <ProjectSection 
          title={Constants.CP_COMPANY}
          subtitle={"2021"}
          currentTitle={hoverTitle}
          linkTo={"/cp_company"}
          narrowWindow={narrowWindow}
          hoverOn={() => setHoverTitle(Constants.CP_COMPANY)}
          hoverOff={() => setHoverTitle("")}/>
        <ProjectSection 
          title={Constants.SWISS_FILMS} 
          subtitle={"2021"}
          currentTitle={hoverTitle}
          linkTo={"/swiss_films"}
          narrowWindow={narrowWindow}
          hoverOn={() => setHoverTitle(Constants.SWISS_FILMS)}
          hoverOff={() => setHoverTitle("")}/>
      </ImageSection>

      <SectionHeader 
        title={Constants.GRAPHIC_DESIGN}
        currentTitle={hoverTitle}
        supportedTitles={[Constants.EXPERIMENTATION, Constants.YORK_HOUSE_SCHOOL]}/>
      <ImageSection
        narrowWindow={narrowWindow}
        currentTitle={hoverTitle}
        supportedTitles={[Constants.EXPERIMENTATION, Constants.YORK_HOUSE_SCHOOL]}>
        <ProjectSection
          title={Constants.EXPERIMENTATION} 
          subtitle={"2021"}
          currentTitle={hoverTitle}
          linkTo={"/experimentation"}
          narrowWindow={narrowWindow}
          hoverOn={() => setHoverTitle(Constants.EXPERIMENTATION)}
          hoverOff={() => setHoverTitle("")}/>
        <ProjectSection 
          title={Constants.YORK_HOUSE_SCHOOL} 
          subtitle={"2021"}
          currentTitle={hoverTitle}
          linkTo={"/york_house_school"}
          narrowWindow={narrowWindow}
          hoverOn={() => setHoverTitle(Constants.YORK_HOUSE_SCHOOL)}
          hoverOff={() => setHoverTitle("")}/>
      </ImageSection>

      <SectionHeader 
        title={Constants.MISCELLANEOUS}
        currentTitle={hoverTitle}
        supportedTitles={[Constants.AKWSTUDIOS, Constants.PHOTOGRAPHY]}/>
      <ImageSection
        narrowWindow={narrowWindow}
        currentTitle={hoverTitle}
        supportedTitles={[Constants.AKWSTUDIOS, Constants.PHOTOGRAPHY]}>
        <ProjectSection
          title={Constants.AKWSTUDIOS} 
          subtitle={"2020 - present"}
          currentTitle={hoverTitle}
          linkTo={"/akwstudios"}
          narrowWindow={narrowWindow}
          hoverOn={() => setHoverTitle(Constants.AKWSTUDIOS)}
          hoverOff={() => setHoverTitle("")}/>
        <ProjectSection 
          title={Constants.PHOTOGRAPHY} 
          subtitle={"2021"}
          currentTitle={hoverTitle}
          linkTo={"/photography"}
          narrowWindow={narrowWindow}
          hoverOn={() => setHoverTitle(Constants.PHOTOGRAPHY)}
          hoverOff={() => setHoverTitle("")}/>
      </ImageSection>

      <Footer/>
    </main>
  )
}
