import * as React from "react"
import LeftBox from "../components/left_box"
import RightBox from "../components/right_box"
import AW from "../images/AW.svg"
import { Link } from 'gatsby'
import { StaticImage } from "gatsby-plugin-image"

const blockStyle = {
  display: "flex",
  alignItems: "center",
  flexGrow: 0,
  width: "100%",
  borderBottomStyle: "solid",
  borderWidth: 1
}
const constainerStyle = {
  display: "flex",
  alignItems: "center",
  flexShrink: 1,
  flexGrow: 1
}
const titleStyle = {
  fontFamily: "Poppins",
  fontSize: 25,
  fontWeight: 600,
  flexGrow: 1
}
const titleStyleNarrow = {
  textAlign: "left",
  alignSelf: "stretch",
  paddingLeft: "30px",
  marginBottom: 0,
  marginTop: 50
}
const titleStyleWide = {
  margin: "20px 95px 20px 95px"
}
const bodyStyle = {
  fontFamily: "Poppins",
  fontSize: 16,
  fontWeight: 400
}
const bodyStyleNarrow = {
  margin: "10px 30px 30px 30px"
}
const bodyStyleWide = {
  margin: "20px 50px 20px 10px"
}
const logoStyle = {
  flexGrow: 0,
  margin: 20
}
const boxStyle = {
  display: "flex",
  alignSelf: "stretch",
  alignItems: "center",
  justifyContent: "space-evenly",
  flexDirection: "row",
  borderTopStyle: "solid",
  borderWidth: 1,
  flexGrow: 0
}

const links = [
  <Link to="https://www.google.ca">
    <StaticImage src="../images/instagram.png" style={logoStyle} alt="instagram"/>
  </Link>,
  <Link to="https://www.google.ca/">
    <StaticImage src="../images/linkedin.png" style={logoStyle} alt="linkedin"/>
  </Link>,
  <Link to="https://www.google.ca">
    <StaticImage src="../images/email.png" style={logoStyle} alt="email"/>
  </Link>
]

export default function AboutSnippet(props) {
  return (
    <main style={{...blockStyle, ...props.narrowWindow ? {minHeight: 300} : {minHeight: 500}}}>
      <LeftBox>
        <AW style={logoStyle}/>
      </LeftBox>
      <div style={{...constainerStyle, ...props.narrowWindow ? {flexDirection: "column"} : {flexDirection: "row"}}}>
        <h1 style={{...titleStyle, ...props.narrowWindow ? titleStyleNarrow : titleStyleWide}}>Hi there, I'm Ashley!</h1>
        <p1 style={{...bodyStyle, ...props.narrowWindow ? bodyStyleNarrow : bodyStyleWide}}>I am a UI and graphic designer who finds beauty in simplicity. 
          I strive to create meaningful designs and products that provide effective solutions.</p1>
        { props.narrowWindow ? <div style={boxStyle}>{links}</div> : <div/>}
      </div>
      
      {
        props.narrowWindow ? <div/> : <RightBox>{links}</RightBox>
      }
      
      
    </main>
  )
}

