import * as React from "react"
import { Link } from 'gatsby'

const titleStyle = {
    fontFamily: "Poppins",
    fontWeight: 700,
    fontSize: 15,
    flexGrow: 2,
    textDecoration: "none",
    color: "#000000",
    marginLeft: 90
}
const containerStyle = {
    display: "flex",
    flexDirection: "horizontal",
    height: "160px",
    alignItems: "center",
    flexGrow: 1,
    borderBottomStyle: "solid",
    borderWidth: 1
}
const navigatorStyle = {
    display: "flex",
    flexGrow: 0,
    marginRight: "50px"
}
const navigatorStyleWide = {
    flexDirection: "row",
    gap: "10px 60px",
}
const navigatorStyleNarrow = {
    flexDirection: "column",
    gap: "0px 0px",
}
const linkStyle = {
    fontFamily: "Poppins",
    fontSize: 14,
    fontWeight: 400,
    textDecoration: "none",
    color: "#000000"
}

export default function Header(props) {
    return (
        <main style={containerStyle}>
            <Link to="/" style={titleStyle}>ashley wong</Link>
            <div style={{...navigatorStyle, ...props.narrowWindow ? navigatorStyleNarrow : navigatorStyleWide}}>
                <Link to="/" style={linkStyle}>projects</Link>
                <Link to="/" style={linkStyle}>resume</Link>
                <Link to="/about" style={linkStyle}>about</Link>
            </div>
        </main>
    )
}
