import * as React from "react"
import LeftBox from "../components/left_box"

const blockStyle = {
  display: "flex",
  alignItems: "center",
  flexGrow: 1,
  width: "100%",
  height: 80,
  borderBottomStyle: "solid",
  borderWidth: 1
}
const titleStyle = {
  fontFamily: "Poppins",
  fontSize: 14,
  fontWeight: 500,
  flexGrow: 1,
  flexShrink: 0,
  margin: "10px 100px 10px 95px"
}
const blueText = {
  color: "#4F89D6"
}
const blackText = {
  color: "#000000"
}

function isHovered(props) {
  if (props.supportedTitles == null) return false
  return props.supportedTitles.includes(props.currentTitle) === true
}

export default function SectionHeader(props) {
  return (
    <main style={blockStyle}>
      <LeftBox isHovered={isHovered(props)}/>
      <h1 style={{
        ...titleStyle, 
        ...isHovered(props) ? blueText : blackText}
        }>{props.title}</h1>
    </main>
  )
}

