import * as React from "react"

const blockStyle = {
    display: "flex",
    alignSelf: "stretch",
    alignItems: "center",
    justifyContent: "center",
    flexDirection: "column",
    width: 255,
    flexGrow: 0,
    borderLeftStyle: "solid",
    borderWidth: 1,
}

export default function RightBox({children}) {
    return (
        <div style={blockStyle}>
            {children}
        </div>
    )
}
  