import * as React from "react"

const containerStyle = {
    display: "flex",
    flexDirection: "row",
    alignItems: "center",
    width: "100%",
    backgroundColor: "#000000"
}

const verticalContainerStyle = {
    width: "100%",
    display: "flex",
    flexDirection: "column"
}

const titleStyle = {
    color: "#FFFFFF",
    fontFamily: "Poppins",
    fontSize: 11,
    fontWeight: 600,
    flexGrow: 1,
    letterSpacing: "0.24em",
    marginLeft: 90,
}

const subtitleStyle = {
    color: "#FFFFFF",
    fontFamily: "Poppins",
    fontSize: 12,
    fontWeight: 300,
    letterSpacing: "0.24em",
    marginRight: 50,
    flexGrow: 0
}

export default function Footer() {
    return (
        <main style={containerStyle}>
            {/* <div style={verticalContainerStyle}> */}
                <h1 style={titleStyle}>DESIGNED BY ASHLEY WONG</h1>
                {/* <h1 style={subtitleStyle}>DEVELOPED BY MARCO LIANG.</h1> */}
            {/* </div> */}

            <h1 style={subtitleStyle}>{":)"}</h1>
        </main>
    )
}
