import * as React from "react"
import LeftBox from "../components/left_box"
import { Link } from "gatsby"
import getImage from "../helpers/get_image"

const blockStyle = {
  display: "flex",
  alignItems: "center",
  flexGrow: 1,
  width: "100%",
  height: 210,
  borderBottomStyle: "solid",
  borderWidth: 1
}
const titleStyle = {
  fontFamily: "Mogena",
  fontSize: "calc(50px + 1.5vw)",
  fontWeight: 250,
  flexGrow: 0,
  marginLeft: 97,
  width: "40%",
  wordWrap: "break-word"
}
const hollow = {
  color: "#F6F0E7",
  textShadow: "-1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000",
  textDecoration: "none"
}
const solid = {
  color: "#4F89D6",
  textShadow: "none",
  textDecoration: "none"
}
//left sidebar year number
const subtitleStyle = {
  fontFamily: "Poppins",
  fontSize: 16,
  fontWeight: 400,
  flexGrow: 1,
  transform: "rotate(180deg)",
  writingMode: "vertical-lr",
  textAlign: "center"
}
const blueText = {
  color: "#4F89D6"
}
const blackText = {
  color: "#000000"
}
const imageOverlayStyle = {
  height: "100%",
  width: "100%"
}

function isHovered(props) {
  return props.currentTitle === props.title
}

export default function ProjectSection(props) {
  return (
    <main style={{...blockStyle, ...props.narrowWindow ? {height: 400} : {}}}>
      <LeftBox>
        <p style={{
          ...subtitleStyle,
          ...isHovered(props) ? blueText : blackText}}>{
            props.narrowWindow ? props.title + " /" + props.subtitle : props.subtitle
          }</p>
      </LeftBox>
      {
        props.narrowWindow ? 
          <Link 
            to={props.linkTo} 
            style={imageOverlayStyle} 
            onMouseEnter={props.hoverOn}
            onMouseLeave={props.hoverOff}
          >
            {getImage(props.title)}
          </Link>
          :
            <Link
              to={props.linkTo}
              style={{...titleStyle, ...isHovered(props) ? solid : hollow}} 
              onMouseEnter={props.hoverOn}
              onMouseLeave={props.hoverOff}
            >
              {props.title}
            </Link> 
      }

    </main>
  )
}

