import * as React from "react"

const boxStyle = {
    display: "flex",
    alignSelf: "stretch",
    alignItems: "center",
    justifyContent: "center",
    flexDirection: "column",
    width: 90,
    flexGrow: 0,
    flexShrink: 0
}

const emptyBox = {
    borderRightStyle: "solid",
    borderColor: "black",
    borderWidth: 1
}

const filledBox = {
    borderRightStyle: "solid",
    borderWidth: 1,
    borderColor: "black",
    backgroundColor: "#4F89D6"
}

export default function LeftBox(props) {
    return (
        <div style={{
            ...boxStyle,
            ...props.style,
            ...props.isHovered ? filledBox : emptyBox}}>
            {props.children}
        </div>
    )
}

LeftBox.defaultProps = {
    isHovered: false
}